    onSelectAllClick() {
        if (this.allAreSelected)
            this.namesSelected = [];
        else
            this.namesSelected = [...this.allNames];
    }
    onItemCheckboxClick(id) {
        const indexInSelected = this.namesSelected.indexOf(id);
        const isSelected = indexInSelected >= 0;
        if (isSelected)
            this.namesSelected.splice(indexInSelected, 1);
        else
            this.namesSelected.push(id);
    }
    resetSelect() {
        this.namesSelected = [];
        this.checkboxIsActive = false;
