    onTitleClick(index) {
        const by = this.fields[index].field;
        const byIsUpdated = by !== this.listSortField;
        const direction = (byIsUpdated || this.listSortDirection === 'desc') ? 'asc' : 'desc';
        const listSort = { by, direction };
        this.updateListSort(listSort);
    }
    resetSort() {
        const listSort = { by: 'serviceName', direction: 'asc' };
        this.updateListSort(listSort);
    }
